.navigation{
position: absolute;
    top: 3em;
    right: 3em;
}
.navigation ul{
    list-style-type: none;
    list-style-position: outside;
}
.navigation li{
    padding-left: 0.5em;
    display: inline-block;
    list-style: none outside; 
    font-size: 2vw; 
}

.navigation a{
    text-decoration: none;
    color: #ffdb4d;
    border: 2px solid #ffdb4d;
    border-radius: 8px;
    padding: 0.2em;
    text-shadow: 1px 1px #1a1400;
}
.navigation a:hover{
    background: #1a1400;
    color: grey;
    border: 2px solid gray;

}